<template>
    <section>
        <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" :widgets="[]" IdentityColumn="resource_id" :HeaderText="$t('Resource')"
            :listType="['List']" v-on:ActionButtonClick="actionButtonClick" :ListData="ResourceList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchResources"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <a class="active">
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                            v-if="data.row.STATUS_NAME == 'Active'">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                            v-if="data.row.STATUS_NAME == 'Inactive'">
                            {{ $t('Inactive') }}
                        </span>
                    </a>
                </template>
            </template>
        </tg-list>
        <AddSAResourcePopup v-if="AddSAResourcePopup" :CrewResId="CrewResId"></AddSAResourcePopup>
        <AddMapResourcePopup v-if="AddMapResourcePopup" :CrewResId="CrewResId"></AddMapResourcePopup>
    </section>
</template>
<script>
import Schema from "@/views/ServiceAppointment/MapResourceSchema";
import DataServices from "@/services/DataService";
import AddSAResourcePopup from '../../components/Modal/AddSAResourcePopup.vue'
import AddMapResourcePopup from '../../components/Modal/AddMapResourcePopup.vue'
export default {
    components: { AddSAResourcePopup, AddMapResourcePopup },
    data() {
        return {
            AddSAResourcePopup: false,
            AddMapResourcePopup: false,
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            GroupStart: 1,
            GroupEnd: 3,
            listActions: Schema.listActions,
            Headers: [{
                "COLUMN_NAME": "resource_name",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Resource Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "UserName",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "User",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "ResourcesType",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Resource type",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5
            },
            ],
            listheaderbuttons: Schema.buttons,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            search: '',
            GroupStart: 1,
            GroupEnd: 3,
            leftSearchFields: null,
            ResourceList: [],
            CrewResId: 0,
            user_id: null,
            companyId: null
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID
        vm.user_type = this.GetUserInfo.UserType
        vm.companyId = this.GetUserInfo.CompanyId;
        await vm.FetchResources();
    },
    methods: {
        FetchResources: function () {
            var vueObj = this;
            var url = `Sa_Id=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            DataServices.GetFsmResourceListBySA_Id(url).then(
                function (response) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'STATUS_NAME') {
                            column.DISPLAY_NAME = 'Status'
                            column.settings = {
                                isInSlot: true,
                                isForTag: true
                            }
                        }
                    });
                    vueObj.ResourceList = [];
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            vueObj.ResourceList = response.data.result.data;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;

                            if (vueObj.ResourceList.length > 0) {
                                vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                                vueObj.NoRecord = false;
                            }
                            else {
                                vueObj.NoRecord = true;
                            }
                        }
                        else {
                            vueObj.NoRecord = true;
                        }                       
                        setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                    }
                });
        },
        leftCommonSearch: function (value) {
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('search')) {
                this.search = params['search'];
            }
            else {
                this.search = '';
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchResources();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchResources();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Delete':
                    this.UnMapById(item.resource_id)
                    break;
            }
        },
        UnMapById: function (id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmUnMap"), true, (vm.$t("UnMap") + "  " + vm.$t("Resource")), function () {
                var postJSON = {
                    SA_id: id,
                    ResourceId: id,
                    Type: 'UnMap'
                };
                var objectPostString = JSON.stringify(postJSON);
                DataServices.MapUnMapResource(objectPostString).then(response => {
                    if (response.data.status == 'Success') {
                        var rslt = JSON.parse(response.data.result);
                        if (rslt.MSG == 'UNMAPPED') {
                            vm.ShowAlert(vm.$t('UnMapSuccess', [vm.$t('Resource')]), "Success", true, vm.$t('Alert'));
                            vm.FetchResources();
                        }
                        else {
                            ShowAlert(String.Format(Globalize.localize('AddedError'), Globalize.localize('Resource')), true, Globalize.localize('Alert'));
                        }
                    }
                });
            });
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchResources();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "CommonAddResource":
                    this.openPopupForAddSAResource(null);
                    break;
                case "CommonMapResource":
                    this.openPopupForMapSAResource(null);
                    break;
            }
        },
        openPopupForAddSAResource: function (id) {
            if (id == undefined)
                id = 0;
            this.AddSAResourcePopup = !this.AddSAResourcePopup;
        },
        openPopupForMapSAResource: function (id) {
            if (id == undefined)
                id = 0;
            this.AddMapResourcePopup = !this.AddMapResourcePopup;
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
    },
}
</script>