<template>
    <div id="divMainFormData" v-cloak>
        <section id="main-content" class="main-content">
            <loader :is-visible="isLoading"></loader>
            <div class="left-menu-tab div-click-event hr_jobpost_a">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition" v-for="(item,index) in CrewList" :key="index">
                        <span class="p-name text-white textelipsis">{{ item.CREW_NAME }}</span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab responivecustomtab" id="myTab" role="tablist">
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 1 }"
                            data-bs-target="#tab-detail-content" href="javascript:void(0)" data-bs-toggle="tab" role="tab"
                            id="tab-Details" v-on:click="loadData('detail');">
                            <em class="fa fa-list" aria-hidden="true" aria-selected="true"></em>
                            {{ $t('Details') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 2 }"
                            data-bs-target="#tab-resource-content" href="javascript:void(0)" id="tab-resource-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Resource');">
                            <em class="fa fa-users" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('Resource') }}
                        </a>
                    </li>
                </ul>
            </div>
            <div class="right-content">
                <div class="col-md-12 p-0 mb-2" v-if="isDetail">
                    <div class="theme-primary partition p-actions-expand">
                        <span class="p-name text-white" id="span_ForSelectedTabName">{{ tabNameTitle }}</span>
                        <input type="hidden" id="hdnActionName" v-bind:value="Acctab" />
                        <span class="p-actions float-right">
                            <RouterLink to="/ServiceAppointment/index" class="p-action-btn text-white float-right">
                                <em class="fa fa-angle-double-left"></em>
                                <br />
                                {{ $t('BacktoList') }}
                            </RouterLink>
                        </span>
                        <span class="p-actions float-right">
                            <a @click="EditServiceAppointment()" class="p-action-btn text-white float-right">
                                <em class="fa fa-pencil"></em>
                                <br />
                                {{ $t('EditServiceAppointment') }}
                            </a>
                        </span>
                        <span class="collapse-head-btns">
                            <a href="javascript:;" class="collapse-btns">
                                <em class="fa fa-angle-up text-white"></em>
                            </a>
                        </span>
                    </div>
                </div>
                    <div class="card mb-3 probx" v-if="isDetail">
                        <div class="card-header">
                            <h2>{{ $t('Details') }}</h2>
                            <div class="cont_right ">
                                <div class="bar">
                                </div>
                            </div>
                        </div>
                        <div class="card-body">
                            <div class="panel1" v-for="(group,gIndex) in LeftViewData" :key="gIndex">
                                <div class="heading-border">
                                    <h5 class="h5 mb-2"><span>{{ GetLocalizedValue(group.group_name, '') }}</span></h5>
                                </div>
                                <div class="row">
                                    <div class="col-md-6 col-lg-3" v-for="(item, iindex) in group.LeftViewData" :key="iindex">
                                        <div class="form-group">
                                            <label>
                                                <b>{{ GetLocalizedValue(item.name, item.display_name) }}: </b>
                                                <span class="answer rightlabeltext"
                                                    v-if="item.dt_code == 'date' || item.dt_code == 'datetime' && item.dt_code != 'textarea'">{{
                                                        item.value | formatDateTime }}</span>
                                                <span class="answer rightlabeltext"
                                                    v-if="item.dt_code == 'decimal' && item.dt_code != 'textarea'">{{
                                                        isBlank(item.value) ? "0.00" : item.value | currencyformat }}</span>
                                                <span class="answer rightlabeltext"
                                                    v-if="item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'decimal' && item.picklist_options != 'Lookup' && item.dt_code != 'textarea'">{{
                                                        isBlank(item.value) ? "--" : (item.value) }}</span>
                                                <span class="answer rightlabeltext"
                                                    v-if="item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'decimal' && item.picklist_options != 'Lookup' && item.dt_code != 'textarea'">{{
                                                        isBlank(item.value) ? "--" : (item.value) }}</span>
                                                <span class="answer rightlabeltext"
                                                    v-if="item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'decimal' && item.picklist_options == 'Lookup' && item.dt_code != 'textarea'">{{
                                                        isBlank(item.value) ? "--" : (FilterDDLandNormalValue(item.value))
                                                    }}</span>
                                                <span class="answer rightlabeltext" v-if="item.dt_code == 'textarea'">{{
                                                    isBlank(item.value) ? "--" :
                                                    (RemoveHtmlTagsOnDescription(item.value)) }}</span>
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 2 }"
                    id="tab-resource-content" role="tabpanel" aria-labelledby="tab-resource-content" v-if="isResource">
                    <ResourceSADatamodal :ResourceTypeId="ticketSourceId"></ResourceSADatamodal>
                </div>
            </div>
        </section>
        <AddServiceAppointmentPopUpVue v-if="showPopupAddServiceAppointment" :SchduleObj="SchduleObj"></AddServiceAppointmentPopUpVue>
    </div>
</template>
<script>
import ResourceSADatamodal from '../../views/ServiceAppointment/ResourceSAData.vue'
import AddServiceAppointmentPopUpVue from '../WorkOrder/AddServiceAppointmentPopUp.vue';
import DataServices from "@/services/DataService";
export default {
    components: { ResourceSADatamodal,AddServiceAppointmentPopUpVue },
    data() {
        return {
            LeftViewData: [],
            isLoading: false,
            isDetail: false,
            isResource: false,
            NoRecord: false,
            ResourceSADatamodal: false,
            tabNameTitle: '',
            showPopupAddServiceAppointment:false
        }
    },
    created: function () {
        this.loadData("detail");
        this.FetchData();

    },
    methods: {
        FetchData: function () {
            var vm = this;
            var url = `id=${this.$route.params.id}&moduleName=Ticketing&subModuleCode=SERVICE_APPOINTMENT`;
            DataServices.ViewServiceAppointment(url).then(
                function (response) {
                    var fullLeftJSON = response.data.LEFTDATA;
                    var ticketJSON = response.data.TICKETDETAIL;
                    if (response.data.TICKETDETAIL > 0) {
                        vm.ticketsubject = response.data.TICKETDETAIL[0].TICKET_SUBJECT;
                        vm.ticketpriority = response.data.TICKETDETAIL[0].PRIORITY_NAME;
                        vm.ticketcategory = response.data.TICKETDETAIL[0].TICKET_CATEGORY_NAME;
                        vm.ticketnumber = response.data.TICKETDETAIL[0].TICKET_NUMBER;
                        vm.assignedto = response.data.TICKETDETAIL[0].ASSIGNED_TO;
                        vm.Worktype = response.data.TICKETDETAIL[0].WORKTYPE_NAME;
                        vm.cdc = response.data.TICKETDETAIL[0].CDC;
                    }
                    fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                        return (element.form_field_visibility == 'YES');
                    });
                    if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                        var groupId = fullLeftJSON[0].group_id;
                        $.each(fullLeftJSON, $.proxy(function (index, item) {
                            if (item.name == 'SA_Number') {
                                vm.SA_Number = item.value;
                            }
                            if (item.name == 'ticket_subject') {
                                item.value = vm.ticketsubject;
                            }
                            if (item.name == 'ticket_priority') {
                                item.value = vm.ticketpriority;
                            }
                            if (item.name == 'ticket_category') {
                                item.value = vm.ticketcategory;
                            }
                            if (item.name == 'SA_Related_Object_Id') {
                                item.value = vm.ticketnumber;
                            }
                            if (item.name == 'assigned_to') {
                                item.value = vm.assignedto;
                            }
                            if (item.name == 'worktype_name') {
                                item.value = vm.Worktype;
                            }
                            if (item.name == 'cdc_name') {
                                item.value = vm.cdc;
                            }
                            if (item.name == 'Status') {
                                vm.SAStatus = item.value;
                            }
                            var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                returnedData = returnedData.sort(function (a, b) {
                                    var a1 = a.display_order, b1 = b.display_order;
                                    if (a1 == b1) return 0;
                                    return a1 > b1 ? 1 : -1;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LeftViewData: returnedData
                                    };
                                    vm.LeftViewData.push(objGroup);
                                }
                            }
                            groupId = item.group_id;
                        }, this));
                    }
                    vm.additionalOwnerPermission = response.data.PERMISSION_DATA;
                    vm.isDetail = true;
                });
            vm.isDetail = true;
        },
        loadData: function (tabName, callFrom) {
            var vm = this;
            if (tabName == "detail") {
                vm.isResource = false;
                vm.isDetail = true;
                vm.activeKey = 1;
            } else if (tabName == "Resource") {
                vm.isDetail = false;
                vm.isResource = true;
                vm.tabNameTitle = 'Resource';
                vm.activeKey = 2;
                vm.openPopupForSAResourceDetail(this.$route.params.id);
            }
        },
        RemoveHtmlTagsOnDescription: function (value) {
            if (value != null) {
                var description = '';
                description = value.replace(/<\/?[^>]+(>|$)/g, "");
                return description;
            }
        },
        EditServiceAppointment() {
            this.showPopupAddServiceAppointment=true;    
            this.SchduleObj={
                SA_Id:this.$route.params.id
            };             
        },
        openPopupForSAResourceDetail: function (id) {
            this.ResourceSADatamodal = !this.ResourceSADatamodal;
        }
    },
}
</script>