<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('MapResource') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <!-- <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditResourceType')"></div>
                                </div>
                            </div>
                        </span> -->
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            isLoading: false,
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('ServiceResource'),
                            name: "ServiceResource",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: "",
                            validationRules: { "required": true },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        this.getResourceList();
    },
    methods: {
        getResourceList: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'FSM_SA_RESOURCENAME'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        Close: function () {
            this.$parent.AddMapResourcePopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var serviceResourceId = '';
            var i = 0;
            for (i = 0; i < formfields.ServiceResource.length; i++) {
                if (i == 0) {
                    serviceResourceId = formfields.ServiceResource[i].value;
                } else {
                    serviceResourceId = serviceResourceId + "," + formfields.ServiceResource[i].value;
                }
            }
            var jsonobj = {
                SA_id: vm.$route.params.id,
                ResourceId: serviceResourceId,
                Type: 'Map'
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveMapResource(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY_MAPPED') {
                        vm.ShowAlert(vm.$t("ResourceAlreadyMapped", [vm.$t("ResourceName")]), "failure", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t("ResourceMapSuccessfully", [vm.$t("Resource")]), "success", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchResources();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>