<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('ManageResource') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <!-- <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditResourceType')"></div>
                                </div>
                            </div>
                        </span> -->
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('ServiceAppointment'),
                            name: "ServiceAppointment",
                            value: "",
                            disabled: true,
                            validationRules: { "required": true }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('User'),
                            name: "User",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectUser'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ResourceType'),
                            name: "ResourceType",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectResource'),
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            mode: "Status",
                            label: this.$t('Status '),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            mode: "Description",
                            label: this.$t('Description '),
                            name: "Description",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Description'),
                            validationRules: "",
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        this.GetDdlRecords_ResourceType();
        this.GetDdlRecords_User();
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            (vueObj.FormSchema)[0].Data[0].value = `SA-${this.$route.params.id}`;
        },
        GetDdlRecords_User: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'FSM_USERS'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[1].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetDdlRecords_ResourceType: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'FSM_RESOURCE_TYPE'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[2].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        Close: function () {
            this.$parent.AddSAResourcePopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                Id: vm.ResourceTypeId == null ? 0 : vm.ResourceTypeId,
                SA_id: vm.$route.params.id,
                ResourceName: "",
                FSMUserListId: formfields.User,
                ResourcetypeId: formfields.ResourceType,
                Desc: formfields.Description,
                Status: formfields.Status
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveResource(params).then((response) => {
                if (response.data.status == 'Success') {
                    if (vm.ResourceTypeId > 0) {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Resource")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Resource")]), "success", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchResources();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>