<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('ServiceAppointment') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="OnSubmit">
                        </dynamic-form>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="py-2">
                                    <small class="text-danger">{{ $t('MandatoryString') }}</small>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService'
export default {
    props: {
        SchduleObj: {
            Type: Object,
            required: true
        },
        callfrom: {
            Type: String,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            WorkOrderListData: [],
            StaticObj: {
                layoutType: "double",
                Data: [
                    {
                        astype: "MultiSelectField",
                        label: this.$t('ServiceResource'),
                        name: "ServiceResource",
                        value: "",
                        mode: "tag",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: []
                        },
                    },
                    {
                        astype: "MultiSelectField",
                        label: this.$t('Service Crew'),
                        name: "ServiceCrew",
                        value: "",
                        mode: "tag",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: []
                        },
                    }
                ]
            },
            FormSchema: [],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
            resourceList: [],
            crewList: [],
            SAData: [],
            isFsmLocating: '',
            SAFormSchema: [],
            StatusId: '1001',
            StatusData: [],
            DurationData: [],
            Id: '',
            ServiceAppointmentList: [],
            JsonObj:[]
        }
    },
    created: async function () {  
        if(this.SchduleObj !=null){            
            this.Id=this.SchduleObj.SA_Id;    
            await this.getForm();
            await this.getResourceCrewData(this.Id);    
        }else{
            await this.getForm();
        }        
        await this.getResourceList();
        await this.getcrewList();        
    },
    methods: {
        Close() {
            this.$parent.showPopupAddServiceAppointment = false;
        },
        getResourceCrewData:async function(ths){
            var vueObj = this;
            var selectedResources;
            var selectedCrew;
            var url = `SA_ID=${ths}`;
            await DataService.getResourceCrewData(url).then(response => {                
                $.each(response.data.result.data, function (index, element) {
                      selectedResources = $.grep(response.data.result.data, function (element, index) {
                          return (element.ResourceType == 'RESOURCE');
                      });
                      selectedCrew = $.grep(response.data.result.data, function (element, index) {
                          return (element.ResourceType == 'CREW');
                      });
                  });
                  if (selectedResources != undefined) {
                    selectedResources.forEach(item=>{
                        let Obj={
                            label:item.name,
                            value:item.value
                        }
                        vueObj.FormSchema[2].Data[0].value.push(Obj);
                    })
                  }
                  if (selectedCrew != undefined) {
                    selectedCrew.forEach(item=>{
                        let Obj={
                            label:item.name,
                            value:item.value
                        }
                        vueObj.FormSchema[2].Data[1].value.push(Obj);
                    })
                  }
            });
        },
        getResourceList: async function () {
            var url = `moduleName=Ticketing&type=FSM_SA_RESOURCENAME&refId=`;
            await DataService.GetDDLData(url).then(response => {
                this.resourceList = response.data.DATA;
                this.resourceList.forEach(item => {
                    let obj = {
                        name: item.name,
                        value: item.value
                    };
                    this.FormSchema[2].Data[0].config.options.push(obj);
                });
            })
        },
        getcrewList: async function () {
            var url = `moduleName=Ticketing&type=FSM_CREWNAME&refId=`;
            await DataService.GetDDLData(url).then(response => {
                this.crewList = response.data.DATA;
                this.crewList.forEach(item => {
                    let obj = {
                        name: item.name,
                        value: item.value
                    };
                    this.FormSchema[2].Data[1].config.options.push(obj);
                });
            })
        },
        async getForm() {
            var vm = this;
            var url = `id=${vm.Id}&moduleName=Ticketing&subModuleCode=SERVICE_APPOINTMENT&langCode=en`;
            await DataService.ManageServiceAppointment(url).then(response => {
                vm.FormSchema = vm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);                                
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (group.group_name == "General Information") {  
                            if (field.name == "SA_Description") {
                                field.astype = "TextAreaField";
                            }
                        }
                        if (group.group_name == "Scheduled Times") {                            
                            if (field.name == "ArrivalWindowStartTime" || field.name == "ArrivalWindowEndTime" || field.name == "SA_Scheduled_StartTime" || field.name == "SA_Scheduled_EndTime") {
                                field.astype = "DateField";
                            }

                        }
                        if (field.name == "SA_Related_Object_Id") {
                            field.config.options = [];
                            field.config.onSearchChange = vm.onSearch;
                            field.config.onChange = vm.onChangeEventForFields;
                        }                         
                        else if(field.name=='ArrivalWindowStartTime' || field.name=='SA_Scheduled_StartTime'){  
                            field.config.mode='DateTime';  
                            field.config.format = 'MM/DD/YYYY h:mm A';                   
                            field.config.onChange = vm.GetStartData;
                        }
                        else if(field.name=='ArrivalWindowEndTime' || field.name=='SA_Scheduled_EndTime'){  
                            field.config.mode='DateTime';  
                            field.config.format = 'MM/DD/YYYY h:mm A';                            
                            field.config.minDate = "";
                        }
                        else {
                            field.config.onChange = vm.onChangeEventForFields;
                        }
                    });
                });
                vm.FormSchema.push(vm.StaticObj);
            });
        },
        GetStartData: function (event, id) {
            var vueObj = this;           
            vueObj.FormSchema[1].Data[1].config.minDate= event;
            vueObj.FormSchema[1].Data[3].config.minDate= event;
        },
        onChangeEventForFields: function (x, y, z) {
            var vm = this;
        },
        onSearch: function (e, selctedValue, search) {
            var vm = this;
            vm.searchSelectData(search, vm, selctedValue);
        },
        searchSelectData: function (search, selctedValue) {
            var vm = this;
            this.ShowSearchData(search, vm, selctedValue);
        },
        setSelectedValue: async function (event, id, val) {
            var vm = this;
            vm.selectvalue = '';
            if (val.value != '') {
                vm.selectvalue = val.value;
            }
            else {
                vm.selectvalue = vm.FormSchema[0].Data[0].value;
            }
        },
        ShowSearchData: async function (search, selctedValue) {
            var vm = this;
            var appendingUrl = `id=&&moduleName=Ticketing&type=SA_TICKET_NUMBER&search=${search}&refId=`;
            if (typeof (selctedValue) == 'undefined') {
                selctedValue = 0;
            }
            vm.FormSchema[0].Data[1].config.options = [];
            await DataService.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    vm.JsonObj = response.data;
                    if (vm.JsonObj != null) {
                        response.data.result.DATA.forEach((item) => {
                            var obj = {
                                value: item.value,
                                name: item.name
                            }
                            vm.FormSchema[0].Data[1].config.options.push(obj);
                        })
                    }
                }
            });
        },
        OnSubmit(formfields, val) {            
            var ths = this;
            ths.isLoading = true;
            var CustomFieldArray = ths.ConvertDynamicFormObjToCustomFieldObj(ths.FormSchema);
            var data = '';
            var dataValue = '';
            var SA_WorkOrder_Id = '';
            CustomFieldArray.forEach((x) => {
                if (x.field_name == "SA_WorkOrder_Id") {
                    if (!ths.isBlank(x.field_value)) {
                        SA_WorkOrder_Id = x.field_value;
                    }
                }
                if (x.field_name == "SA_Id") {
                    if (ths.id>0) {                      
                        x.field_value =x.field_value;
                    }else{
                        x.field_value =0;
                    }
                }
                if (x.field_name == "SA_Related_Object_Id") {                  
                    if (!ths.isBlank(x.field_value)) {
                        x.field_value = x.field_value.value;
                    }
                }
            });
            formfields.ServiceResource.forEach((itm, index) => {
                if (index == 0) {
                    dataValue = itm.value;
                }
                else {
                    dataValue += ',' + itm.value;
                }
            })
            var SResource = dataValue;
            formfields.ServiceCrew.forEach((itm, index) => {
                if (index == 0) {
                    data = itm.value;
                }
                else {
                    data += ',' + itm.value;
                }
            })
            var SCrew = data;
            var postJSON = {
                data: CustomFieldArray,
                serviceResourceId: SResource,
                serviceCrewId: SCrew
            }
            var objectPostString = JSON.stringify(postJSON);
            if (SA_WorkOrder_Id != null || SA_WorkOrder_Id != '') {
                var url = `Id=${SA_WorkOrder_Id}&SA_Id=${ths.Id}&reqfrom=AddServiceAppointment`;
                DataService.GetServiceAppointmentByWorkOrderId(url).then(response => {
                    ths.ServiceAppointmentList = [];
                    ths.isLoading = false;
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            ths.ServiceAppointmentList = response.data.result.DATA;
                            var arr = "";
                            $.each(ths.ServiceAppointmentList, function (eIndex, eItem) {
                                arr += eItem.SA_NUMBER + ',';
                            });
                            if (arr != "") {
                                var test = ths.$t('ServiceAppointmentCancel') + '(' + arr + ')' + ths.$t('ConfirmServiceAppointmentCancel');
                                ths.confirmR(test, true, null, function () {
                                    DataService.AddEditServiceAppointment(objectPostString).then(response => {                                        
                                        if (response.data.CODE == 1) {
                                            if (ths.Id > 0) {                                                
                                                ths.ShowAlert(ths.$t("UpdatedSuccess", [ths.$t("ServiceAppointment")]), "success", true, ths.$t("Alert"));
                                                ths.Close();
                                                ths.$parent.fetchRecords();
                                            }
                                            else {
                                                ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("ServiceAppointment")]), "success", true, ths.$t("Alert"));
                                                ths.Close();
                                                ths.$parent.fetchRecords();
                                            }
                                        }
                                        else {
                                            ths.ShowAlert(ths.$t("AddedError", [ths.$t("ServiceAppointment")]), "failure", true, ths.$t("Alert"));                                            
                                        }
                                        ths.isLoading=false;
                                    })
                                })
                            }
                            else {
                                DataService.AddEditServiceAppointment(objectPostString).then(response => {                                        
                                    if (response.data.CODE == 1) {
                                        if (ths.Id > 0) {                                                
                                            ths.ShowAlert(ths.$t("UpdatedSuccess", [ths.$t("ServiceAppointment")]), "success", true, ths.$t("Alert"));
                                            ths.Close();
                                            ths.$parent.fetchRecords();
                                        }
                                        else {
                                            ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("ServiceAppointment")]), "success", true, ths.$t("Alert"));
                                            ths.Close();
                                            ths.$parent.fetchRecords();
                                        }
                                    }
                                    else {
                                        ths.ShowAlert(ths.$t("AddedError", [ths.$t("ServiceAppointment")]), "failure", true, ths.$t("Alert"));                                            
                                    }
                                    ths.isLoading=false;
                                })
                            }
                        }
                    }
                });
            }
        }
    }
}
</script>